* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
}

body {
    background: #eee;
    text-align: center;
    padding: 25px;
}

.container {
    background-color: antiquewhite;
    border: 1px solid #9e2c2c;
    border-radius: 0.75rem;
    padding: 1rem;
    margin-bottom: 1rem;
    min-height: 250px;
}